/*  Projects  */
.projects .title::after {
  content: "What I did ?";
  background: #121FCF;
background: linear-gradient(to right, #121FCF 0%, #CF1512 100%);
background-clip: text;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;

  font-weight: 900;
