  text-align: center;
  overflow: hidden;
  transition: all 0.3s ease;
}

.projects .carousel .card .box {

  transition: all 0.3s ease;
}

.projects .carousel .card:hover .box {
  transform: scale(1.05);
}

.projects .carousel .card .text {
  font-size: 30px;
  font-weight: 990;
  margin: 10px 0 7px 0;
}

.projects .carousel .card img {
  height: 150px;
